import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

interface CoinMarketCapPayload {
  data: Data;
  metadata: Metadata;
}

interface Metadata {
  timestamp: number;
  error?: any;
}

interface Data {
  id: number;
  name: string;
  symbol: string;
  website_slug: string;
  rank: number;
  circulating_supply: number;
  total_supply: number;
  max_supply?: any;
  quotes: Quotes;
  last_updated: number;
}

interface Quotes {
  [key: string]: CurrencyData;
}

interface CurrencyData {
  price: number;
  volume_24h: number;
  market_cap: number;
  percent_change_1h: number;
  percent_change_24h: number;
  percent_change_7d: number;
}

@Injectable({
  providedIn: 'root'
})
export class CurrencyConverterService {

  private apiUrl = 'https://api.coinmarketcap.com';

  constructor(
    private http: HttpClient,
  ) { }

  private getIDFromCurrency(currency: string) {
    return environment.supportedCurrencies.find((x) => x.ticker === currency).apiId;
  }

  convert({ currency, value }, toCurrency: 'BTC' | 'tBTC') {
    const toCurrencyID = this.getIDFromCurrency(toCurrency);
    const currencyReg = /^t?(.*)$/; // TODO(simon) use a safer way to query amount from test net to main net
    return this.http.get<CoinMarketCapPayload>(
      `${this.apiUrl}/v2/ticker/${toCurrencyID}/?convert=${currencyReg.exec(currency)[1]}`
    )
      .pipe(map(({ data: { quotes } }) => {
        const changeRate = Number(quotes[currency].price);
        return {
          currency: toCurrency,
          value: (Number(value) / changeRate).toFixed(6),
        };
      }));
  }
}
