import { Injectable } from '@angular/core';
import { KeyFactoryService } from '../key-factory/key-factory.service';
import { Observable, ReplaySubject, BehaviorSubject } from 'rxjs';
import { map, shareReplay, switchMap, tap } from 'rxjs/operators';
import { AddressValidation } from '@interblockchain/validator/dist/address-validation';
import { ValidationService } from '../validation/validation.service';
import { LedgerService } from '../ledger/ledger.service';
import { uuidv4 } from '../../utils';
import { CurrencyConverterService } from '../currency-converter/currency-converter.service';
import { environment } from '../../../environments/environment';
import { CurrencySelectionService } from '../currency-selection/currency-selection.service';

const netPrefix = (network: string) => `t${network}`;

const mockData = {
  total: { value: 55, currency: 'USD' }, methodData: [{
    supportedMethods: 'https://carte7000-payment-demo.herokuapp.com/pay', data: {
      apiKey: '85732589752hjfslkjhf',
      supportedTokens: ['tBTC']
    }
  }]
};

export interface Transaction {
  id: string;
  amount: number;
  nbConf: number;
  network: string;
  toAddress: string;
  original: {
    currency: string;
    value: string;
  };
}

export interface PaymentRequestData {
  total: {
    currency: string,
    value: number,
  };
  methodData: {
    supportedMethods: string,
    data: { apiKey: string, supportedTokens: string[] }
  }[];
}

@Injectable({
  providedIn: 'root'
})
export class PaymentSwService {
  // private swMessageSubject = new BehaviorSubject<PaymentRequestData>(mockData);
  private swMessageSubject = new ReplaySubject<PaymentRequestData>();
  private augmentedSwMessage = this.swMessageSubject.pipe(
    switchMap(({ total, methodData }) => {
      return this.currenySelection.selectedCurrency.pipe(
        switchMap((selectedCurrency: string) => {
          return this.currencyConverter.convert(total, selectedCurrency as any)
            .pipe(
              map((converted) => ({ converted, original: total, methodData, selectedCurrency }))
            );
        }));
    }),
    shareReplay()
  );
  public currentTransaction: Observable<Transaction>;
  public currentValidation: Observable<AddressValidation>;
  private paymentRequestClient: ServiceWorker;

  private details;

  private writeTransactionInLedger = switchMap<Transaction, Transaction>((tx) => {
    return this.augmentedSwMessage.pipe(
      switchMap(({ methodData, selectedCurrency }) => {
        const { data: { apiKey } } = methodData.find((x) => x.supportedMethods === environment.methodName);
        const uuid = uuidv4();
        return this.ledger.createAccount({
          accountID: uuid,
          currency: selectedCurrency,
          folio: tx.toAddress,
          note: 'test'
        }, apiKey).pipe(
          tap(console.log),
          switchMap(() => {
            return this.ledger.createTx({
              accountID: uuid,
              amount: tx.amount,
              currency: selectedCurrency,
              provider: 'test',
              from: 'test',
              initialValue: '0'
            }, apiKey).pipe(
              map(_ => tx)
            );
          }));
      }));
  });

  private watchingConfirmationsAndMapToValidation = switchMap((
    { amount, id, nbConf, network, toAddress }
  ) => {
    return this.validation.watch({ address: toAddress, amount, id, nbConf, network });
  });

  private setDetails = tap<AddressValidation>((v) => {
    if (v.isCompleted) {
      this.details = {
        amount: v.info.amount,
        address: v.info.addresses[0].address,
        txHash: v.info.txHash,
      };
    }
  });

  private getPaymentKeyAndCreateTransaction = switchMap(({ original, converted: { currency, value } }) => {
    return this.keyFactory.getKey(uuidv4(), currency).pipe(
      map((({ key }) => ({
        toAddress: key,
        amount: value,
        nbConf: 0,
        network: currency.toUpperCase(),
        id: uuidv4(),
        original,
      }))),
    );
  });

  constructor(
    private keyFactory: KeyFactoryService,
    private validation: ValidationService,
    private ledger: LedgerService,
    private currencyConverter: CurrencyConverterService,
    private currenySelection: CurrencySelectionService
  ) {
    this.currentTransaction = this.augmentedSwMessage.pipe(
      this.getPaymentKeyAndCreateTransaction,
      shareReplay()
    );


    this.currentValidation = this.currentTransaction.pipe(
      this.writeTransactionInLedger,
      this.watchingConfirmationsAndMapToValidation,
      this.setDetails
    );
  }

  init() {
    if (navigator.serviceWorker) {
      navigator.serviceWorker.addEventListener('message', (e: { source: any, data: PaymentRequestData }) => {
        this.paymentRequestClient = e.source;
        this.swMessageSubject.next(e.data);
        console.log(e);
      });
    }

    if (navigator.serviceWorker.controller) {
      navigator.serviceWorker.controller.postMessage('payment_app_window_ready');
    }
  }

  public confirmPayment() {
    const paymentAppResponse = {
      methodName: 'https://carte7000-payment-demo.herokuapp.com/pay',
      details: this.details,
    };

    this.paymentRequestClient.postMessage(paymentAppResponse);
  }

  private cancelPaymentRequest() {
    if (!this.paymentRequestClient) {
      return;
    }

    this.paymentRequestClient.postMessage('The payment request is cancelled by user');
    window.close();
  }
}
