import { Injectable } from '@angular/core';
import { Subject, ReplaySubject, Observable, BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CurrencySelectionService {

  private _selectedCurrency = new ReplaySubject();

  public get selectedCurrency() {
    // return new BehaviorSubject('tBTC');
    return this._selectedCurrency.asObservable();
  }

  constructor() { }

  select(currency: string) {
    this._selectedCurrency.next(currency);
  }
}
